import { useMemo } from "react";
import type { ScreenMembersOnMfaSmsEnrollment } from "@auth0/auth0-acul-js";
import MfaSmsEnrollment from "@auth0/auth0-acul-js/mfa-sms-enrollment";
import { executeSafely } from "@/utils/helpers/executeSafely";

export const useMfaSmsEnrollmentManager = () => {
  const mfaSmsEnrollmentInstance = useMemo(() => new MfaSmsEnrollment(), []);
  const { user, transaction, screen } = mfaSmsEnrollmentInstance;

  // Get phone from user_metadata if available
  const phoneFromMetadata = user?.userMetadata?.phone || "";

  const handleContinueEnrollment = async (phone: string): Promise<void> => {
    const options = {
      phone: phone?.trim() || "",
    };
    executeSafely(
      `Continue MFA SMS enrollment with phone: ${options.phone}`,
      () => mfaSmsEnrollmentInstance.continueEnrollment(options)
    );
  };

  return {
    mfaSmsEnrollmentInstance,
    user,
    transaction,
    screen,
    phoneFromMetadata,
    handleContinueEnrollment,
    errors: mfaSmsEnrollmentInstance.getError(),
    texts: (screen?.texts || {}) as ScreenMembersOnMfaSmsEnrollment["texts"],
  };
};