import ULThemeCard from "@/components/ULThemeCard";
import ULThemePageLayout from "@/components/ULThemePageLayout";
import { useState } from "react";
import { useMfaSmsEnrollmentManager } from "./hooks/useMfaSmsEnrollmentManager";

function MfaSmsEnrollmentScreen() {
  const {
    phoneFromMetadata,
    handleContinueEnrollment,
    errors,
    texts,
    user,
  } = useMfaSmsEnrollmentManager();

  // Use email property directly
  const email = user?.email || "";

  const [phone, setPhone] = useState(phoneFromMetadata);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    handleContinueEnrollment(phone);
  };

  return (
    <ULThemePageLayout className="theme-universal">
      <ULThemeCard className="w-full max-w-[400px] gap-4">
        <h2 className="text-xl font-bold mb-2">
          {texts?.title || "MFA Enrollment"}
        </h2>
        <div className="mb-2">
          <strong>Email:</strong> {email || "No email available"}
        </div>
        <form onSubmit={handleSubmit} className="flex flex-col gap-4">
          <label>
            Phone Number
            <input
              type="tel"
              value={phone}
              onChange={e => setPhone(e.target.value)}
              className="input"
              required
            />
          </label>
          {Array.isArray(errors) && errors.length > 0 && (
            <div className="text-red-600">
              {errors.map((err, idx) => (
                <div key={idx}>{err.message}</div>
              ))}
            </div>
          )}
          {!Array.isArray(errors) && errors && (
            <div className="text-red-600">{errors || String(errors)}</div>
          )}
          <button type="submit" className="btn btn-primary">
            {texts?.continueButton || "Continue"}
          </button>
        </form>
        <div className="mt-4">
          <h3 className="font-semibold">User Object</h3>
          <pre className="bg-gray-100 p-2 rounded text-xs overflow-x-auto">
            {user ? JSON.stringify(user, null, 2) : "No user data"}
          </pre>
        </div>
      </ULThemeCard>
    </ULThemePageLayout>
  );
}

export default MfaSmsEnrollmentScreen;